<template>
	<div class="flex">
		
		<VHeader/>

		<div class="container mx-auto">
			<div class="gallery px-5 py-20 h-screen overflow-auto">	
				<div class="gallery__title">
					<h1 class="info__title text-white text-3xl font-semibold capitalize mb-10 pb-4 pr-10 border-b border-b-white inline-flex after:w-12 after:h-[4px] after:bg-white after:absolute after:left-0 after:bottom-[-3px] relative">Суратлар топлами</h1>
				</div>
				<div class="gallery__list columns-3 sm:columns-4 gap-0">
					
					<a 						
						class="gallery__col cursor-pointer block"
						@click.prevent="openGallery(index)"
						v-for="(img, index) in galleries"
						:key="index"
					>
						<img :src="img" alt="image">
					</a>				

					<VueEasyLightbox
						class="backdrop-blur"
						:visible="boxIsOpen"
						:index="index"
						:imgs="galleries"
						@hide="hideGallery"
					>
					</VueEasyLightbox>
				</div>
			</div>
		</div>
	</div>
</template>



<script>

import { ref } from 'vue-demi';
import VueEasyLightbox from "vue-easy-lightbox";
import VHeader from '@/components/v-header.vue'

export default {
	data() {
		return {
			galleries: [
				require('../assets/images/gallery/photo_2022-05-09_16-55-29.jpg'),
				require('../assets/images/gallery/photo_2022-05-09_16-55-35.jpg'),
				require('../assets/images/gallery/photo_2022-05-09_16-55-39.jpg'),
				require('../assets/images/gallery/photo_2022-05-09_16-55-40.jpg'),
				require('../assets/images/gallery/photo_2022-05-09_16-55-42.jpg'),
				require('../assets/images/gallery/photo_2022-05-09_16-55-44.jpg'),
				require('../assets/images/gallery/photo_2022-05-09_16-55-45.jpg'),
				require('../assets/images/gallery/photo_2022-05-09_16-55-48.jpg'),
				require('../assets/images/gallery/photo_2022-05-09_16-55-47.jpg'),
				require('../assets/images/gallery/photo_2022-05-09_16-55-50.jpg'),
				require('../assets/images/gallery/photo_2022-05-09_16-55-51.jpg'),
				require('../assets/images/gallery/photo_2022-05-09_16-55-37.jpg'),
			]
		}
	},	
	setup() {
		const index = ref(0);
		const boxIsOpen = ref(false);

		const hideGallery = () => {
			boxIsOpen.value = false;
		};

		const openGallery = (imageIndex) => {
			index.value = imageIndex;
			boxIsOpen.value = true;
		};
		return { hideGallery, openGallery, boxIsOpen, index } 
	},	
	methods: {
		
	},
	components: {
		VHeader,
		VueEasyLightbox
	},
}

</script>

<style scope>
	.overflow-auto::-webkit-scrollbar {
		display: none;
	}	
</style>