import { createRouter, createWebHistory } from "vue-router";
import HomeView from "../views/HomeView.vue";
import CoursView from "../views/CoursView.vue";
import ContactsView from "@/views/ContactsView.vue";
import PortfoliosViewVue from "@/views/PortfoliosView.vue";
import AboutView from "@/views/AboutView.vue";
import ProjectViewVue from "@/views/ProjectView.vue";
import GalleryView from "@/views/GalleryView.vue";


const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView,
  },
  {
    path: "/about",
    name: "about-me",
    component: AboutView,
  },
  {
    path: "/portfolios",
    name: "portfolios",
    component: PortfoliosViewVue,
  },
  {
    path: "/project-view/:slug",
    name: "project-view",
    component: ProjectViewVue,
  },
  {
    path: "/courses",
    name: "cours",
    component: CoursView,
  },
  {
    path: "/gallery",
    name: "gallery",
    component: GalleryView,
  },
  {
    path: "/contacts",
    name: "contacts",
    component: ContactsView,
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
